#home {
    position: relative;
    background: var(--bg-main);
    background-size: cover;
    background-repeat: repeat;
    background-position: center;

    .app-wrapper {
      padding: 0;

      .copyright {
        display: none;
      }
    }
}

.app__header {
    flex: 1;
    width: 100%;
    height: 100%;
    flex-direction: row;

    padding: 3rem 2rem 0;

    @media screen and (min-width: 2000px) {
        padding-top: 8rem;
    }
    @media screen and (max-width: 1600px) {
      padding: 4rem 0 0 0;
    }
    @media screen and (max-width: 1200px) {
        flex-direction: column;
        padding-top: 4rem;
    }
    @media screen and (max-width: 800px) {
      padding: 2rem 0;
  }
    @media screen and (max-width: 450px) {
        padding: 4rem 1rem 2rem;
    }
}

.app__header-info {
    flex: 0.65;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;

    @media screen and (max-width: 2000px) {
        width: 100%;
        margin-right: 0rem;
    }
}

.app__header-badge {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    .badge-cmp, .tag-cmp {
        padding: 1rem 3rem;
        border: var(--white-color);
        background-color: var(--header-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;
        box-shadow: 0 0 20px var(--box-shadow);
    }

    .badge-cmp {
      @media screen and (min-width: 2000px) {
        width: 600px;
      }

      @media screen and (max-width: 2000px) {
        padding: 10px 20px;
        width: 380px;
      }

      @media screen and (max-width: 600px) {
        width: auto;
    }

    }

    .tag-cmp {
        flex-direction: column;
        margin-top: 3rem;

        p {
            width: 100%;
            text-transform: uppercase;
            text-align: right;
        }

        @media screen and (max-width: 1200px) {
            margin-top: 2rem;
            flex-direction: row;
            align-items: flex-end;
        }
    }

    span {
        font-size: 2.5rem;

        @media screen and (min-width: 2000px) {
            font-size: 5rem;
        }

        @media screen and (min-width: 1200px) {
            padding-left: 0;
        }
    }

    @media screen and (max-width: 1200px) {
        justify-content: flex-start;
        align-items: flex-start;
    }
}

.app__intro-badge {
    width: 100%;
    padding: 5rem;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    .badge-cmp2, .tag-cmp {
        padding: 1rem 2rem;
        border: var(--white-color);
        background-color: var(--header-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;
        box-shadow: 0 0 20px var(--box-shadow);

        @media screen and (min-width: 2000px) {
          font-size: 2rem;
        }

        @media screen and (max-width: 600px) {
            padding: 1rem;
            font-size: 1.1rem;
        }
    }
    
    .badge-cmp3 {
        padding: 1rem 2rem;
        margin-top: 2rem;
        border: var(--white-color);
        background-color: var(--header-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;
        box-shadow: 0 0 20px var(--box-shadow)
    }

    @media screen and (max-width: 1500px) {
        justify-content: flex-start;
        align-items: flex-start;
        padding: 2rem;
    }

    @media screen and (max-width: 1200px) {
        padding: 2rem 0;
    }

    
}

.intro-div {
    padding: 5rem;
}

.app__header-circles {
    flex: 0.75;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
    margin-left: 1rem;

    div {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: var(--header-color);
        box-shadow: 0 0 20px var(--box-shadow);

        img {
            width: 60%;
            height: 60%;
        }
    }

    div:nth-child(1) {
        width: 90px;
        height: 90px;
    }
    div:nth-child(2) {
        margin-left: 7rem;
        width: 110px;
        height: 110px;
    }
    div:nth-child(3) {
        margin-left: 12rem;
        width: 160px;
        height: 160px;
    }
    div:nth-child(4) {
        margin-left: 7rem;
        width: 110px;
        height: 110px;

        img {
          filter: var(--next-image);
        }

    }
    div:nth-child(5) {
        width: 90px;
        height: 90px;
    }

    @media screen and (min-width: 2000px) {
        div:nth-child(1) {
            width: 150px;
            height: 150px;
        }
        div:nth-child(2) {

            width: 170px;
            height: 170px;
        }
        div:nth-child(3) {
            width: 200px;
            height: 200px;
        }
    }

    @media screen and (max-width: 1500px) {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 0;

        div:nth-child(1) {
            width: 90px;
            height: 90px;
        }
        div:nth-child(2) {
            margin-left: 0;
            width: 110px;
            height: 110px;
        }
        div:nth-child(3) {
            margin-left: 0;
            width: 160px;
            height: 160px;
        }
        div:nth-child(4) {
            margin-left: 0;
            width: 110px;
            height: 110px;
        }
        div:nth-child(5) {
            width: 90px;
            height: 90px;
        }
    }

    @media screen and (max-width: 800px) {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 0;

        div:nth-child(1) {
            width: 60px;
            height: 60px;
        }
        div:nth-child(2) {
            margin-left: 0;
            width: 70px;
            height: 70px;
        }
        div:nth-child(3) {
            margin-left: 0;
            width: 80px;
            height: 80px;
        }
        div:nth-child(4) {
            margin-left: 0;
            width: 70px;
            height: 70px;
        }
        div:nth-child(5) {
            width: 60px;
            height: 60px;
        }
    }

    @media screen and (max-width: 400px) {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 0;

        div:nth-child(1) {
            width: 50px;
            height: 50px;
        }
        div:nth-child(2) {
            margin-left: 0;
            width: 50px;
            height: 50px;
        }
        div:nth-child(3) {
            margin-left: 0;
            width: 50px;
            height: 50px;
        }
        div:nth-child(4) {
            margin-left: 0;
            width: 50px;
            height: 50px;
        }
        div:nth-child(5) {
            width: 50px;
            height: 50px;
        }
    }
}


.app__header-img {
    flex: 1;
    height: 100%;

    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;

    img {
        width: 100%;
        object-fit: contain;
        z-index: 1;
    }

   .overlay_circle {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        height: 90%;
   }

   @media screen and (max-width: 1200px) {
        margin: 2rem 0;
   }
}

